nav .container-mobile {
  display: none; }

nav .nav__container {
  display: none; }

.nav__line, .nav__line-left {
  background-color: #EAEAEA;
  border-radius: 1px;
  width: 2px;
  height: 32px;
  display: inline-block; }

.nav__icon {
  width: 40px;
  height: 40px; }

.nav__first-block, .nav__second-block {
  display: flex;
  align-items: center; }

@media (max-width: 767px) {
  nav {
    background: #FFFFFF;
    height: 88px; }
    nav .nav__container {
      display: flex;
      justify-content: space-between;
      padding-top: 18.59px; }
  .nav__icon--call, .nav__icon--chat, .nav__icon--profile, .nav__line {
    display: none; }
  .nav__line-left {
    order: 3;
    margin-left: 16.73px; }
  .nav__icon--hamburger, .nav__logo, .nav__line {
    margin-left: 16px; }
  .nav__icon--repair, .nav__icon--checkstatus {
    margin-right: 16px; }
  main {
    margin-top: 24px; } }

@media (min-width: 768px) and (max-width: 1439px) {
  nav {
    background: #FFFFFF;
    height: 88px; }
    nav .nav__container {
      display: flex;
      justify-content: space-between;
      padding-top: 18.59px; }
  main {
    margin-top: 24px; }
  .nav__second-block .nav__line {
    margin-left: 20px;
    margin-right: 20px; }
  .nav__icon--call, .nav__icon--chat {
    margin-right: 16px; }
  .nav__line-left {
    margin-left: 20px; }
  .nav__icon--hamburger {
    margin-left: 24px; }
  .nav__logo, .nav__line {
    margin-left: 20.73px; }
  .nav__second-block {
    margin-right: 24px; }
  .nav__icon--repair {
    margin-right: 16px; } }
