.last-paragraph, .last-paragraph-cta {
  font-size: 14px;
  font-family: "TT Lakes";
  color: #1B1C21; }

.last-paragraph-cta {
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  font-style: normal; }
  .last-paragraph-cta img {
    transform: translate(7px, 2px); }

footer {
  font-size: 14px;
  color: #7E7E82;
  font-family: "TT Lakes"; }

.footer__p1, .footer__p2 {
  font-family: "TT Lakes"; }

@media (max-width: 767px) {
  .last-paragraph, .last-paragraph-cta {
    padding: 16px; }
  footer {
    margin-bottom: 19px;
    margin-top: 40px;
    text-align: center; }
  .footer__p1, .footer__p2 {
    margin-bottom: 16px; }
  .footer__p3 {
    margin: 0 auto;
    width: 90%; } }

@media (min-width: 768px) and (max-width: 1439px) {
  .last-paragraph {
    padding-top: 24px;
    padding-left: 24px;
    width: 440px; }
    .last-paragraph-cta {
      padding-top: 16px;
      padding-left: 24px; }
  footer {
    display: flex;
    justify-content: space-between;
    margin-top: 80px;
    padding-left: 24px;
    padding-bottom: 41px; }
  .footer__p1 {
    width: 218px; }
  .footer__p3 {
    width: 233px;
    text-align: right;
    padding-right: 24px; } }

@media (min-width: 1440px) {
  .last-paragraph {
    padding-top: 32px;
    padding-left: 32px;
    width: 440px; }
    .last-paragraph-cta {
      padding-top: 16px;
      padding-left: 32px; }
  footer {
    display: flex;
    justify-content: space-between;
    margin-top: 80px;
    padding-left: 31px;
    padding-bottom: 21px; }
  .footer__p1 {
    width: 218px; }
  .footer__p3 {
    width: 333px;
    text-align: right;
    padding-right: 24px; } }
