.services {
  display: none; }
  .services__line {
    width: 4px;
    height: 40px;
    background: #41F6D7;
    border-radius: 0 4px 4px 0; }
  .services__services-list {
    margin-left: 30px;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 28px;
    font-weight: bold;
    align-self: center;
    font-family: "TT Lakes";
    font-style: normal;
    color: #1B1C21; }

@media (max-width: 767px) {
  .services {
    display: flex; }
    .services__services-list,
    .services .nav-1120__services-list {
      margin-left: 12px;
      margin-top: 0;
      margin-bottom: 0;
      font-size: 28px;
      font-weight: bold;
      align-self: center; } }

@media (min-width: 768px) and (max-width: 1439px) {
  .services-block {
    display: flex; }
    .services-block__services-list {
      margin-left: 20px;
      margin-top: 0;
      margin-bottom: 0;
      font-size: 28px;
      font-weight: bold;
      align-self: center; } }
