.scroll-blocks {
  display: flex;
  align-items: center;
  overflow-x: auto;
  overflow-y: hidden;
  position: relative;
  font-size: 16px; }
  .scroll-blocks p {
    font-family: "TT Lakes";
    font-weight: 500;
    font-style: normal;
    margin-top: 24px;
    flex: 0 0 auto;
    margin-left: 19.56px; }
  .scroll-blocks .scroll-blocks__block--highlighted {
    width: 246px;
    height: 40px;
    background: #FFFFFF;
    display: flex;
    color: #7E7E82;
    justify-content: center;
    border-radius: 4px;
    border: 2px solid #B8FFEC;
    align-items: center;
    margin-left: 16px !important; }
  .scroll-blocks .scroll-blocks__block {
    color: #7E7E82; }

.scroll-shadow {
  background: linear-gradient(270deg, #F8F8F8 16.84%, rgba(248, 248, 248, 0.0001) 100%);
  position: absolute;
  width: 24px;
  height: 40px;
  right: 0;
  top: 176px; }

@media (min-width: 768px) and (max-width: 1439px) {
  .scroll-blocks__block--highlighted {
    margin-left: 24px !important; }
  .scroll-blocks p {
    margin-top: 32px; }
  .scroll-shadow {
    background: linear-gradient(270deg, #F8F8F8 16.84%, rgba(248, 248, 248, 0.0001) 100%);
    top: 185px; } }

@media (min-width: 1440px) {
  .scroll-shadow {
    display: none; }
  .scroll-blocks {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    width: 100%; }
    .scroll-blocks p {
      margin-top: 8px;
      flex: 0 0 auto;
      margin-left: 19.56px; }
    .scroll-blocks__block--highlighted {
      margin-left: 32px !important; } }

@media (min-width: 1440px) and (max-width: 1546px) {
  .scroll-blocks__block:last-child {
    margin-left: 46.5px;
    margin-top: 14px; } }
