.text-and-image__main-text {
  width: 288px;
  font-size: 14px;
  line-height: 18px;
  font-family: "TT Lakes";
  font-weight: normal;
  font-style: normal; }

.text-and-image__main-text-container {
  display: flex;
  margin-top: 24px;
  margin-left: 16px;
  color: #1B1C21; }

.text-and-image__read-more {
  display: flex;
  align-items: center;
  margin-left: 16px;
  margin-top: 16px;
  font-size: 16px;
  cursor: pointer; }

.text-and-image__read-more-icon {
  width: 24px;
  height: 24px; }

.text-and-image__read-more p {
  font-family: "TT Lakes";
  font-weight: 500;
  font-style: normal;
  color: #1B1C21; }

.text-and-image__additional-text, .text-and-image__additional-text2 {
  display: none; }

.text-and-image__hidden-text {
  display: none; }

.show-text {
  display: inline-block;
  margin-top: 24px;
  -webkit-animation: vanish 1s;
          animation: vanish 1s; }

@-webkit-keyframes vanish {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes vanish {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@media (max-width: 767px) {
  .text-and-image__main-img {
    margin-top: 16px; } }

@media (min-width: 768px) and (max-width: 1439px) {
  .text-and-image {
    display: flex;
    justify-content: space-between; }
    .text-and-image__text-and-read-more {
      display: flex;
      flex-direction: column; }
    .text-and-image__text-and-read-more, .text-and-image__main-img {
      margin-top: 32px; }
    .text-and-image__additional-text {
      display: inline-block;
      margin-top: 24px; }
    .text-and-image__main-img {
      width: 360px;
      height: 208px;
      margin-right: 25px;
      border-radius: 5px; }
    .text-and-image__main-text-container {
      margin-top: 0;
      margin-left: 24px; }
    .text-and-image__read-more {
      margin-left: 25px;
      margin-top: 10px; } }

@media (min-width: 850px) and (max-width: 1439px) {
  .text-and-image {
    display: flex;
    justify-content: space-evenly; } }

@media (min-width: 1120px) {
  .text-and-image {
    display: flex;
    margin-top: 7px;
    justify-content: space-around; }
    .text-and-image__additional-text {
      display: inline-block;
      margin-top: 24px; }
    .text-and-image__main-img {
      width: 531.35px;
      height: 307px;
      border-radius: 5px; }
    .text-and-image__main-text {
      width: 490px;
      font-size: 14px;
      line-height: 18px; }
    .text-and-image__main-text-container {
      margin-top: 16.22px;
      margin-left: 16px; }
    .text-and-image__additional-text2 {
      display: inline; } }

@media (min-width: 1440px) {
  .text-and-image {
    margin-top: 30px;
    display: flex;
    justify-content: space-evenly; } }
