@media (min-width: 768px) and (max-width: 1439px) {
  .nav__icon {
    cursor: pointer;
    transition: transform 0.3s ease; }
  .nav__icon:hover {
    transform: scale(1.2); } }

.nav-1120 {
  display: none; }
  .nav-1120__line {
    width: 4px;
    height: 40px;
    background: #41F6D7;
    border-radius: 0 4px 4px 0; }
  .nav-1120__services-list {
    margin-left: 30px;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 28px;
    font-weight: bold;
    align-self: center;
    font-family: "TT Lakes";
    font-weight: bold;
    font-style: normal;
    color: #1B1C21; }

@media (min-width: 1440px) {
  .nav-1120 {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .nav-1120__buttons {
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #1B1C21; }
      .nav-1120__buttons span {
        margin-right: 8px;
        font-family: "TT Lakes";
        font-weight: 500;
        font-style: normal; }
      .nav-1120__buttons .nav-1120__icon {
        margin-right: 32px;
        cursor: pointer;
        transition: transform 0.3s ease; }
      .nav-1120__buttons .nav-1120__icon:hover {
        transform: scale(1.2); }
    .nav-1120__services {
      display: flex;
      align-items: center; } }
