@media (max-width: 767px) {
  .services-prices {
    display: none; } }

.services-prices__header {
  margin-top: 31px;
  margin-left: 40px;
  display: flex;
  margin-bottom: 16px;
  font-size: 14px;
  color: #7E7E82;
  font-family: "TT Lakes"; }
  .services-prices__header p:nth-of-type(2) {
    margin-left: 246px; }
  .services-prices__header p:nth-of-type(3) {
    margin-left: 61px; }

.services-prices__services {
  width: 720px;
  height: 324px;
  background: #FFFFFF;
  margin-left: 24px;
  border-radius: 5px;
  font-size: 14px;
  color: #1B1C21;
  font-family: "TT Lakes"; }

.services-prices__service {
  display: flex;
  margin-bottom: 16px;
  padding-top: 16px;
  align-items: center; }
  .services-prices__service p:nth-of-type(1) {
    margin-left: 16px; }
  .services-prices__service p:nth-of-type(2) {
    margin-left: 238px; }
  .services-prices__service p:nth-of-type(3) {
    margin-left: 49px; }
  .services-prices__service button {
    margin-left: 20px; }

.services-prices hr {
  width: 688px;
  margin: 0 auto;
  background-color: #EAEAEA;
  height: 1px;
  border: none; }

.services-prices__btn button {
  cursor: pointer;
  transition: transform 0.3s ease; }

.services-prices__btn button:hover {
  transform: scale(1.1); }

@media (min-width: 800px) and (max-width: 1439px) {
  .services-block {
    justify-content: center; }
  .services-prices__services {
    margin: 0 auto !important; }
  .services-prices__header {
    justify-content: center;
    margin-left: -195px; } }

@media (min-width: 1440px) {
