      margin-left: 132px; }
  .services-prices hr {
    width: 96%; }
  .services-prices__header {
    justify-content: center;
    margin-left: -295px; }
    .services-prices__header p:nth-of-type(2) {
      margin-left: 358px; }
    .services-prices__header p:nth-of-type(3) {
      margin-left: 173px; } }
